.EditItem {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 995;
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 0;
    -webkit-animation: fadeIn 250ms linear forwards;
            animation: fadeIn 250ms linear forwards;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.EditItem .overlay {
    background: #000;
    opacity: 0;
    position: fixed;
